/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useEffect, useState } from 'react';
import { TableRow } from './TableRow';

export const Table = ({ products, handleSort, direction }) => {
  const [headFields, setheadFields] = useState();

  useEffect(() => {
    setheadFields(['ID', 'Product', 'Category', 'User']);
  }, []);

  return (
    <div className="box table-container">
      <table
        data-cy="ProductTable"
        className="table is-striped is-narrow is-fullwidth"
      >
        {products.length ? (
          <>
            <TableHead
              headFields={headFields}
              handleSort={handleSort}
              direction={direction}
            />
            <tbody>
              {products.map(prodcut => (
                <TableRow key={prodcut.id} product={prodcut} />
              ))}
            </tbody>
          </>
        ) : (
          <p>No products matching selected criteria</p>
        )}
      </table>
    </div>
  );
};

const TableHead = ({ headFields, handleSort, direction }) => (
  <thead>
    <tr>
      {headFields.map(field => (
        <TableHeadItem
          field={field}
          handleSort={handleSort}
          direction={direction}
        />
      ))}
    </tr>
  </thead>
);

const TableHeadItem = ({ field, handleSort, direction }) => (
  <th>
    <span className="is-flex is-flex-wrap-nowrap">
      {field}
      <a href="#/">
        {/* eslint-disable-next-line jsx-a11y/no-static-element-interactions */}
        <span
          className="icon"
          onClick={() => handleSort(field.toLowerCase())}
        >
          <i
            data-cy="SortIcon"
            className="fa-sort fas"
          />
        </span>
      </a>
    </span>
  </th>
);
