import React, { useEffect, useState } from 'react';
import './App.scss';

import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';
import { Table } from './components/Table';
import { UsersFilter } from './components/UsersFilter';
import { CategoryFilter } from './components/CategoryFilter';
import { ResetButton } from './components/ResetButton';
import { SearchInput } from './components/SearchInput';

const data = productsFromServer.map((product) => {
  const category = categoriesFromServer.find(
    c => c.id === product.categoryId,
  );
  const user = usersFromServer.find(u => u.id === category.ownerId);

  return {
    ...product,
    category: { ...category },
    user: { ...user },
  };
});

export const App = () => {
  const [products, setProducts] = useState([]);
  const [users, setUsers] = useState([]);
  const [categories, setCategories] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [filters, setFilters] = useState({
    name: '',
    category: '',
  });
  const [sortType, setSortType] = useState({
    sortBy: '',
    dir: 0,
  });

  useEffect(() => {
    if (sortType.sortBy) {
      getSortedData(sortType.sortBy, sortType.dir);
    }
  }, [sortType]);

  const handleSort = (sortBy) => {
    setSortType((prew) => {
      let dir = sortType.dir + 1;

      if (dir > 1) {
        dir = -1;
      }

      if (sortBy !== prew.sortBy) {
        dir = 1;
      }

      return {
        sortBy,
        dir,
      };
    });
  };

  const getSortedData = (sortBy, dir) => {
    let sortField = sortBy;

    if (dir === 0) {
      sortField = 'id';
    }

    setProducts((prev) => {
      const newProducts = [...prev].sort((productA, productB) => {
        switch (sortField) {
          case 'id':
            return productA.id - productB.id;
          case 'product':
            return productA.name.localeCompare(productB.name);
          case 'category':
            return (
              productA.category.title.localeCompare(productB.category.title)
            );
          case 'user':
            return productA.user.name.localeCompare(productB.user.name);
          default:
            return 0;
        }
      });

      return dir === -1 ? newProducts.reverse() : newProducts;
    });
  };

  useEffect(() => {
    setProducts(data);
    setUsers(usersFromServer.map(user => ({ ...user })));
    setCategories(categoriesFromServer.map(category => ({ ...category })));
  }, []);

  useEffect(() => {
    setProducts(getFilteredData());
  }, [filters, searchQuery]);

  const getFilteredData = () => {
    let filteredData = [...data];

    if (filters.name) {
      filteredData = [
        ...filteredData.filter(product => product.user.name === filters.name),
      ];
    }

    if (filters.category) {
      filteredData = [
        ...filteredData.filter(
          product => product.category.title === filters.category,
        ),
      ];
    }

    if (searchQuery) {
      filteredData = [
        ...filteredData.filter(product => (
          product.name.toLowerCase().includes(searchQuery.toLocaleLowerCase())
        )),
      ];
    }

    return filteredData;
  };

  const setFilterName = (name) => {
    setFilters(prew => ({ ...prew, name }));
  };

  const setCategory = (category) => {
    setFilters(prew => ({ ...prew, category }));
  };

  const handleSearch = (event) => {
    setSearchQuery(event.target.value);
  };

  const clearSearchQuery = () => {
    setSearchQuery('');
  };

  const handleReset = () => {
    setFilters({ name: null });
    clearSearchQuery();
  };

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        {/* start filters */}
        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <UsersFilter users={users} setFilterName={setFilterName} />
            <SearchInput
              query={searchQuery}
              handleSearch={handleSearch}
              clearSearchQuery={clearSearchQuery}
            />
            <CategoryFilter categories={categories} setCategory={setCategory} />
            <ResetButton handleReset={handleReset} />
          </nav>
        </div>

        <Table
          products={products}
          handleSort={handleSort}
          direction={sortType.dir}
        />
      </div>
    </div>
  );
};
