// import className from "classnames";

// has-text-link `has-text-danger`

export const TableRow = ({ product }) => {
  const { id, name, category, user } = product;
  const categoryStr = `${category.icon} - ${category.title}`;
  const nameColor = user.sex === 'm' ? 'has-text-link' : `has-text-danger`;

  return (
    <tr data-cy="Product">
      <td
        data-cy="ProductId"
        className="has-text-weight-bold"
      >
        {id}
      </td>

      <td
        data-cy="ProductName"
      >
        {name}
      </td>
      <td data-cy="ProductCategory">
        {categoryStr}
      </td>

      <td
        data-cy="ProductUser"
        className={`${nameColor} has-text-link`}
      >
        {user.name}
      </td>
    </tr>
  );
};
